html{height:100%;font-family:'Quicksand', sans-serif}body{background-color:white}canvas{display:block;position:fixed;left:0;top:0;width:100%;height:100%}audio{position:fixed;left:10px;bottom:10px;width:40%;z-index:3}@-webkit-keyframes bg{0%{background-position:0 0}100%{background-position:8px 8px}}@keyframes bg{0%{background-position:0 0}100%{background-position:8px 8px}}#file-input{position:fixed;top:10px;left:10px;z-index:3}#file-input::-webkit-file-upload-button{visibility:hidden;color:transparent;display:none;opacity:0}#file-input::before{content:'Select a Song';display:flex;align-self:center;align-content:center;margin-top:10px;margin-bottom:0px;margin-left:10px;padding-top:14px;padding-bottom:14px;padding-left:58px;border-radius:10px;border:0;background-color:#5f79af;align-content:center;color:white;font-weight:bolder;font-size:14px;border:1px solid #5f79af;outline:none;-webkit-user-select:none;cursor:pointer;-webkit-appearance:none}#name{position:absolute;top:0;right:20px;z-index:3;color:#eeeeee;font-family:monospace}.welcome-message{position:absolute;font-size:25px}.app-container{margin-bottom:50px}.app-container h1{position:absolute;bottom:10px;right:15px;font-size:50px;margin:0;color:white;text-shadow:0 0 10px rgba(0,0,0,0.3)}.navbar{background-color:#5f79af;overflow:hidden;position:fixed;bottom:15px;left:300px;width:280px;height:25px;border-radius:5px;display:flex;align-items:center;flex-direction:row}.navbar a{float:left;padding:10px;text-decoration:none;font-size:15px;color:white}.navbar a:hover{font-size:20px;color:gray}
